.form{
  &__form{
  border-radius: 20px 20px 0px 0px;
  margin: 0 auto;
  padding: 10px;
  max-width: 370px;
  height: 780px;
  background: linear-gradient(180deg, #000000 -35.95%, #A8291F 100%);
  box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.55);
  }

&__title{
  margin: 0;
  padding: 26px 16px;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
}
&__hidden{
  display: block;
}
&__descr{
  margin: 0;
  padding: 0px 16px 16px 8px;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 15px;
  line-height: 20px;
  text-transform: uppercase;
}
&__input{
  border-radius: 10px;
  background: linear-gradient(90deg, rgba(34, 6, 3, 0.5) 0%, rgba(0, 0, 0, 0) 103.12%);
  max-width: 369px;
  padding: 15px 20px;
  height: 40px;
  left: calc(50% - 369px/2 - 0.5px);
  top: calc(50% - 40px/2 - 243px);
  color: var(--light-color);
  margin-bottom: 15px;
  &::placeholder{
    font-family: 'Rockwell Nova';
    font-weight: 400;
    font-size: 10px;
    line-height: 13px;
    text-transform: capitalize;
    color: var(--light-color);
  }
  &-big{
    min-width: 300px;
  }
  &-small{
    max-width: 100px;
  }
}
&__small-box{
  max-width: 320px;
  display: flex;
  justify-content: space-between;
}
&__checkbox{
  font-family: 'Rockwell Nova';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  margin-bottom: 15px;
}
&__checkbox-inpt{
  margin-left: 20px;
}
&__descr{
  margin: 0;
  padding: 0px 10px 10px 30px;
  font-family: 'Rockwell Nova';
  font-weight: 300;
  font-size: 10px;
  line-height: 13px;
  display: flex;
  align-items: center;
}
&__link{
  color: var(--light-color);
}
&__lable{
  font-family: 'Rockwell Nova';
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
}
&__btn{
  display: block;
  margin: 0 auto !important;
  padding: 14px 0px;
  border-radius: 10px;
  background: #A8291F;
  width: 214px;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 15px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--light-color);
}
&__checkbox-bottom{
  margin-bottom: 15px;
}
}

